.trash-basket-wrapper {
  font-size: 40px;
  text-align: center;
  margin-top: 20px;
}

.trash-basket {
  position: relative;
  display: inline-block;
  width: 50px;
  cursor: pointer;
}

.trash-count {
  position: absolute;
  background: tomato;
  height: 20px;
  width: 20px;
  color: white;
  font-size: 12px;
  border-radius: 50%;
  bottom: 0;
  right: 0;
}

.trash-basket-list {
  background: white;
  position: relative;
  margin-top: 10px;
  font-size: 22px;
  text-align: left;
  z-index: 5;
}
.trash-basket-list:before {
  content: '';
  width: 0;
  height: 0;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-bottom: 15px solid white;
  top: -13px;
  left: 45%;
  position: absolute;
  z-index: 10;
}
.empty-msg-baket {
  text-align: center;
  color: grey;
  padding: 20px;
}
.basket-clear-btn {
  position: absolute;
  top: -30px;
  height: 23px;
  line-height: 8px;
}